.page-container {
    display: inline-block;

    .page {
        background-color: #fff;

        .page-limiter {
            overflow: hidden;

            .page-pages {
                position: relative;

                .page-hidden-data__7ec44e0c-94b2-43a2-8ba0-36fa0066e820 {
                    display: none;
                }
            }

            //~~
            .page-pages {
                //position: absolute;
                //left: 0;
                //top: 0;
                overflow-x: scroll;
                column-gap: 0;
            }

            //~~
            //.page-froala-placeholder {
            .ql-editor {
                margin-top: -1px;
                padding-top: 1px;

                &:focus {
                    outline: 0;
                }

                p {
                    line-height: 1em;
                }
            }
        }
    }
}

.fr-placeholder {
    display: none !important;
}

.ql-container,
.ql-editor {
    margin: 0;
    padding: 0;
}

.ql-editor {
    overflow-y: visible;
    overflow: visible;
}

.ql-tooltip {
    display: none !important;
}
