.document {
    height: 100%;
    background-color: grey;

    .document-pages {
        position: relative;

        .document-caret {
            z-index: 10;
            position: absolute;
            pointer-events: none;
        }

        .document-page-container {
            position: absolute; 
            left: 0; 
            right: 0; 
            margin-left: auto;
            margin-right: auto;
        }
    }
}